import React from 'react'
import Disqus from '../components/Disqus'
import 'katex/dist/katex.min.css'
import { graphql, HeadProps } from 'gatsby'
import Layout from '../components'
import { BackButton } from '../components/Buttons/BackButton'

interface IBlogPost {
  data: {
    markdownRemark: {
      frontmatter: {
        title: string
        date: string
        keywords: string
        description: string
      }

      html: string
      fields: {
        slug: string
      }

      excerpt: string
    }
  }
}

export default function Template({ data }: IBlogPost) {
  const {
    frontmatter,
    html,
    fields: { slug },
  } = data.markdownRemark

  return (
    <Layout>
      <BackButton />
      <section className="post-container">
        <article className="post">
          <header className="post__header">
            <h2>{frontmatter.title}</h2>
            <p>
              <time>{frontmatter.date}</time>
            </p>
          </header>
          <main
            className="post__body"
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </article>

        <Disqus identifier={slug} />
      </section>
    </Layout>
  )
}

export function Head({ data }: HeadProps<IBlogPost['data']>) {
  const { frontmatter, excerpt } = data.markdownRemark
  const description = frontmatter.description || excerpt
  const title = frontmatter.title

  return (
    <>
      <title>{title}</title>
      <meta name="keywords" content={frontmatter.keywords} />
      <meta name="description" content={description} />
      <meta itemProp="name" content={title} />
      <meta itemProp="description" content={description} />
      <meta name="twitter:card" content="summary" />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta name="og:title" content={title} />
      <meta name="og:description" content={description} />
      <meta name="og:site_name" content={title} />
      <meta name="og:locale" content="en_US" />
      <meta name="og:type" content="article" />
      <meta name="article:section" content="Technology" />
      <meta name="article:published_time" content={frontmatter.date} />
      <meta name="article:author" content="Kyung Mo Kweon" />
      <meta name="article:tag" content={frontmatter.keywords} />
    </>
  )
}

export const pageQuery = graphql`
  query PostBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      excerpt
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        title
        keywords
        description
      }
      fields {
        slug
      }
    }
  }
`
